import { walk } from '../../utils/datasource'
import { includes, find, filter, get, difference } from 'lodash'

/**
 *  tree  checked 
 * @param {Array} tree 
 * @param {Array} checked  value 
 * @param {string} strategy 
 * @return {Array}  tree checked 
 */
export function markChecked (tree, checked, strategy) {
  let walker = {
    enter (item, ctx) {
      return markAncestorInChecked(item, checked, ctx)
    },
    exit (item, ctx) {
      let { value, children } = item
      if (hasChildren(item)) {
        item.checked = children.every(({ checked }) => checked)
        item.partialChecked =
          !item.checked &&
          children.some(
            ({ partialChecked, checked }) => !!partialChecked || checked
          )
      } else {
        //  checked
        let includeIndeterminate = strategy === 'allWithIndeterminate'
        item.checked =
          includes(checked, value) ||
          (!includeIndeterminate && !!ctx.ancestorInChecked)
      }
    }
  }
  walk(tree, walker)
  return tree
}

function getItemChecked (
  { value, checked, children, partialChecked },
  strategy,
  childrenCollect
) {
  let collectSelf = checked && value != null
  let collect = []
  switch (strategy) {
    case 'all':
      if (collectSelf) {
        collect.push(value)
      }
      collect.push(...childrenCollect)
      break
    case 'allWithIndeterminate':
      if ((partialChecked || checked) && value != null) {
        collect.push(value)
      }
      collect.push(...childrenCollect)
      break
    case 'branch':
      collect.push(...(collectSelf ? [value] : childrenCollect))
      break
    case 'leaf':
      collectSelf = collectSelf && (!children || !children.length)
      collect.push(...(collectSelf ? [value] : childrenCollect))
      break
  }
  return collect
}

/**
 *  strategy 
 * @param {Array} itemsCopy  normalize 
 * @param {Array} checked 
 * @param {string} strategy 
 * @return {Array}  strategy 
 */
export function getNormalizedChecked (itemsCopy, checked, strategy) {
  let walker = {
    enter (item, ctx) {
      return markAncestorInChecked(item, checked, ctx)
    },
    exit (item, ctx) {
      let { value, children } = item
      let itemChecked = false
      let partialChecked = false
      let childrenCollect = []
      if (hasChildren(item)) {
        itemChecked = ctx.childrenResult.every(i => !!i.checked)
        partialChecked =
          !itemChecked &&
          ctx.childrenResult.some(
            ({ checked, partialChecked }) => !!partialChecked || !!checked
          )
        childrenCollect = ctx.childrenResult.reduce(
          (res, { collect }) => res.concat(collect),
          []
        )
      } else {
        //  checked
        let includeIndeterminate = strategy === 'allWithIndeterminate'
        itemChecked =
          includes(checked, value) ||
          (!includeIndeterminate && !!ctx.ancestorInChecked)
      }
      let collect = getItemChecked(
        { value, children, checked: itemChecked, partialChecked },
        strategy,
        childrenCollect
      )
      return { checked: itemChecked, partialChecked, collect }
    }
  }
  return walk([{ children: itemsCopy }], walker)[0].collect
}

/**
 * toggle 
 * @param {Array} prevChecked  [1,2,3],  toggle value=3 
 * @param {object} item  toggle  value=3 
 * @param {Array} parents  item [topMost, 2ndTop]
 * @param {Array} itemsCopy 
 * @param {string} strategy 
 */
export function toggleItem (prevChecked, item, parents, itemsCopy, strategy) {
  let newChecked = itemCheckCommon(prevChecked, item, parents, { strategy })
  return getNormalizedChecked(itemsCopy, newChecked, strategy)
}

//  toggle toggle  transfer candidate  hidden 
export function checkItem (prevChecked, item, parents, itemsCopy, strategy) {
  let newChecked = itemCheckCommon(prevChecked, item, parents, {
    strategy,
    operation: 'check'
  })
  return getNormalizedChecked(itemsCopy, newChecked, strategy)
}

//  toggle toggle  transfer candidatePanel  hidden 
export function uncheckItem (prevChecked, item, parents, itemsCopy, strategy) {
  let newChecked = itemCheckCommon(prevChecked, item, parents, {
    strategy,
    operation: 'uncheck'
  })
  return getNormalizedChecked(itemsCopy, newChecked, strategy)
}

//  uncheckItem hidden  transfer selectedPanel 
export function clearItem (prevChecked, item, parents, itemsCopy, strategy) {
  let newChecked = itemCheckCommon(prevChecked, item, parents, {
    strategy,
    operation: 'uncheck',
    clear: true
  })
  return getNormalizedChecked(itemsCopy, newChecked, strategy)
}

function batch (prevChecked, itemsCopy, options) {
  let itemsToBatch = filter(
    itemsCopy,
    ({ hidden, disabled }) => !hidden && !disabled
  )
  let newChecked = itemsToBatch.reduce((prevChecked, item) => {
    return itemCheckCommon(prevChecked, item, [], options)
  }, prevChecked)

  //  strategy  checked
  return getNormalizedChecked(itemsCopy, newChecked, options.strategy)
}

// transfer candidatePanel  hidden 
export function checkAll (prevChecked, itemsCopy, strategy) {
  let options = { strategy, operation: 'check' }
  return batch(prevChecked, itemsCopy, options)
}

// transfer selectedPanel 
export function clearAll (prevChecked, itemsCopy, strategy) {
  let newChecked = difference(prevChecked, getEnabledDescendant(itemsCopy))
  return getNormalizedChecked(itemsCopy, newChecked, strategy)
}

/**
 * / item  checked  emit  checked prop
 * @param {Array} prevChecked 
 * @param {object} item 
 * @param {Array} parents 
 * @param {string} option.strategy 
 * @param {string} option.operation checkitemuncheckitemtoggle
 * @param {booleab} option.clear  hidden 
 */
export function itemCheckCommon (
  prevChecked,
  item,
  parents,
  { strategy, operation, clear }
) {
  let checked
  let isGroup = hasChildren(item)
  prevChecked = prevChecked || []
  if (isGroup) {
    // -> 
    let leaves
    if (!operation || isUncheck(operation)) {
      leaves = clear ? getEnabledLeaves(item) : getEnabledCheckedLeaves(item)
      if (!operation && leaves.length) {
        operation = 'uncheck'
      }
    }
    if (!operation || !isUncheck(operation)) {
      leaves = getEnabledUncheckedLeaves(item)
      operation = 'check'
    }
    checked = isUncheck(operation)
      ? uncheckGroup(prevChecked, item, parents, { strategy, leaves })
      : checkGroup(prevChecked, item, { leaves })
  } else {
    operation = operation || (item.checked ? 'uncheck' : 'check')
    checked = isUncheck(operation)
      ? uncheckLeaf(prevChecked, item, parents, { strategy })
      : checkLeaf(prevChecked, item)
  }
  return checked
}

function checkLeaf (prevChecked, item) {
  return [...prevChecked, item.value]
}

function uncheckLeaf (prevChecked, item, parents, { strategy }) {
  let includeIndeterminate = strategy === 'allWithIndeterminate'
  let parentValues = parents.map(i => i.value).filter(val => val != null)
  let willUncheck = [item.value, ...parentValues] //  parent getNormalizedChecked 
  let mostTopAncestor = find(parents, ({ value }) =>
    includes(prevChecked, value)
  )
  let realChecked = !includeIndeterminate
    ? [
      ...prevChecked,
      ...(mostTopAncestor ? getAllLeaves(mostTopAncestor) : [])
    ]
    : prevChecked
  return realChecked.filter(i => !includes(willUncheck, i))
}

function checkGroup (prevChecked, item, { leaves }) {
  return [...prevChecked, ...leaves]
}

function uncheckGroup (prevChecked, item, parents, { strategy, leaves }) {
  let includeIndeterminate = strategy === 'allWithIndeterminate'
  let parentValues = parents.map(i => i.value).filter(val => val != null)
  let willUncheck = [
    ...leaves,
    ...getAllGroup(item),
    ...(item.value != null ? [item.value] : []),
    ...parentValues //  parent getNormalizedChecked 
  ]
  let mostTopAncestor = find(parents, ({ value }) =>
    includes(prevChecked, value)
  )
  //  getAllLeaves
  //  parentValues  branch 
  //  1.  branch 
  //  2. 
  //        all/leaf/branch 
  let realChecked = !includeIndeterminate
    ? [
      ...prevChecked,
      ...(mostTopAncestor ? getAllLeaves(mostTopAncestor) : [])
    ]
    : prevChecked

  return realChecked.filter(i => !includes(willUncheck, i))
}

function getLeaves (item, initialCtx) {
  let result = []
  walk(
    item,
    (child, context) => {
      // 1.  checked disabled  disabled 
      // 2. hidden true  leafAncestor 
      let { disabled, value } = child
      if (hasChildren(child)) {
        if (context.hidden != null && isLeafAncestor(child)) {
          context = { ...context, hidden: null }
        }
        return disabled ? false : context
      }
      if (
        !disabled &&
        meet(child, context, 'checked') &&
        meet(child, context, 'hidden')
      ) {
        result.push(value)
      }
    },
    'children',
    initialCtx
  )
  return result
}

// 
function getEnabledCheckedLeaves (item) {
  return getLeaves([item], { checked: true, hidden: false })
}

function getEnabledLeaves (item) {
  return getLeaves([item], { checked: null, hidden: null })
}

// 
function getEnabledUncheckedLeaves (item) {
  return getLeaves([item], { checked: false, hidden: false })
}

// 
function getAllLeaves (item) {
  let result = []
  walk(item, child => {
    if (!hasChildren(child)) {
      result.push(child.value)
    }
  })
  return result
}

function getAllGroup (item) {
  let result = []
  walk(item, child => {
    let hasCh = hasChildren(child)
    if (hasCh) {
      if (child.value != null) {
        result.push(child.value)
      }
    }
    return hasCh
  })
  return result
}

export function getEnabledDescendant (item) {
  let result = []
  walk(item, child => {
    let { disabled, value } = child
    let hasCh = hasChildren(child)
    if (value != null && !disabled) {
      result.push(value)
    }
    return hasCh && !disabled
  })
  return result
}

//  tree  checked 
export function getCheckedSubTree (tree, checked, strategy) {
  const walker = {
    enter (item, ctx) {
      return markAncestorInChecked(item, checked, ctx)
    },
    exit (item, ctx) {
      if (hasChildren(item)) {
        let children = ctx.childrenResult.filter(i => !!i)
        if (children.length) {
          return {
            ...item,
            children
          }
        }
      } else {
        let includeIndeterminate = strategy === 'allWithIndeterminate'
        let isChecked =
          includes(checked, item.value) ||
          (!includeIndeterminate && !!ctx.ancestorInChecked)
        if (isChecked) {
          return item
        }
      }
    }
  }
  return walk(tree, walker).filter(i => !!i)
}

function hasChildren (item) {
  return !!get(item, 'children.length')
}

function markAncestorInChecked (item, checked, ctx) {
  if (hasChildren(item)) {
    let inChecked = includes(checked, item.value)
    if (inChecked && !ctx.ancestorInChecked) {
      ctx.ancestorInChecked = true
    }
    return ctx
  }
}

function isUncheck (operation) {
  return operation === 'uncheck'
}

function isLeafAncestor (item) {
  return (
    !item.hidden && hasChildren(item) && item.children.every(i => !!i.hidden)
  )
}

function meet (item, context, field) {
  return context[field] == null || context[field] === Boolean(item[field])
}
